tails .coords .header{color:#999;font-size:9px}.pass-details .coords a{color:#000;border-bottom:1px dashed #000;text-decoration:none;cursor:pointer}.westra-passes-description-comment{margin:0!important}.westra-passes-description-comment-author{font-weight:700;color:#777;margin-right:1em!important}