.wikimedia-viewer-container{width:100%;height:100%;display:-webkit-box;display:-webkit-flex;display:-moz-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-webkit-flex-direction:column;-moz-box-orient:vertical;-moz-box-direction:normal;-ms-flex-direction:column;flex-direction:column}.wikimedia-viewer-map-container{width:100%;-webkit-box-flex:1;-webkit-flex-grow:1;-moz-box-flex:1;-ms-flex-positive:1;flex-grow:1}.wikimedia-viewer-map-container.loading{background-repeat:no-repeat;background-position:50% 50%;background-image:url(loading.svg)}.wikimedia-viewer-page-buttons-container{width:100%;background-color:#aaa;white-space:nowrap;overflow-x:auto;overflow-y:hidden;display:none}.wikimedia-viewer-page-buttons-container.enabled{display:block}.wikimedia-viewer-page-button{margin-right:4px;background-color:#777;border:3px solid #777;color:#fff;height:37px;width:37px;line-height:37px;font-family:Arial,Helvetica,sans-serif;font-size:20px;font-weight:700;text-align:center;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;display:inline-block}.wikimedia-viewer-page-button.active{border:3px solid #000}.wikimedia-viewer-info-overlay{position:absolute;bottom:0;right:0;background-color:rgba(0,0,0,.7);color:#fff;font-family:Arial,Helvetica,sans-serif;font-size:11px;padding:4px;text-align:right;z-index:1000;white-space:nowrap;-o-text-overflow:ellipsis;text-overflow:ellipsis;overflow:hidden;max-width:100%;-webkit-box-sizing:border-box;-moz-box-sizing:border-box;box-sizing:border-box}.wikimedia-viewer-info-overlay a{color:#fff}.wikimedia-viewer-button-next,.wikimedia-viewer-button-prev{display:none;cursor:pointer;position:absolute;top:50%;width:40px;height:80px;margin-top:-40px;background-color:#000;opacity:.5;z-index:1000;background-repeat:no-repeat;-webkit-background-size:60px 60px;-moz-background-size:60px;background-size:60px}.wikimedia-viewer-button-next.enabled,.wikimedia-viewer-button-prev.enabled{display:block}.wikimedia-viewer-button-prev{background-image:url(angle-left.svg);left:0;background-position:70% 50%}.wikimedia-viewer-button-next{background-image:url(angle-right.svg);right:0;background-position:30% 50%}